<template>
  <div class="user-info">
    <el-descriptions title="个人信息" :column="3" border>
      <el-descriptions-item v-for="(info, index) in detailInfo" :key="index">
        <template #label>{{ info.key }}</template>
        {{
          info.key == '队长' ? (info.value == true ? '是' : '否') : info.value
        }}
      </el-descriptions-item>
    </el-descriptions>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from 'vue'
import { getUserInfo } from '@/service/user-info'
import { convertUserInfo } from '@/utils/user-info'

export default defineComponent({
  name: 'UserInfo',
  setup() {
    // setup 函数不可以是异步的
    const detailInfo = ref({})
    getUserInfo().then((res) => {
      detailInfo.value = convertUserInfo(res)
    })
    return {
      detailInfo
    }
  }
})
</script>

<style lang="less" scoped>
h2 {
  color: var(--text-color-1);
  font-family: var(--font-family-number);
}
</style>
