interface UserInfoItem {
  key: string
  value: any
}
export function convertUserInfo(userInfo: any): UserInfoItem[] {
  const zhMap = new Map([
    ['username', '用户名'],
    ['name', '昵称'],
    ['department', '部门'],
    ['major', '专业'],
    ['joinTime', '入队事件'],
    ['allTrainningTime', '总训练时长'],
    ['admin', '队长'],
    ['email', '邮箱']
  ])
  const newUserInfo: UserInfoItem[] = []
  Object.keys(userInfo).forEach((key) => {
    newUserInfo.push({
      key: zhMap.get(key) ?? 'null',
      value: userInfo[key]
    })
  })
  return newUserInfo
}
