import { dfRequest } from '../request'
import { ValidResponse } from '../request/types'

export function getUserInfo() {
  return new Promise((resolve) => {
    dfRequest
      .post({
        url: '/api/getuserinfo/'
      })
      .then((res) => {
        // console.log('个人信息', res)
        resolve((res as ValidResponse).data)
      })
      .catch((err) => console.log('获取个人信息失败', err))
  })
}
